:root {
  --turn-color: rgb(254, 127, 0);
  --cost-color: rgb(0, 26, 255);
  --atk-color: rgb(207, 0, 0);
  --heal-color: rgb(0, 186, 0);
  --player-color: #388eff;
  --menu-color: #9b842d;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* viewport width too low */
.modal-viewport-error {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: rgb(0, 0, 0);
  display: none;
  z-index: 1000;
  color: var(--menu-color);
}
/* flex shortcut */
body,
.enemy-section,
.turn-counter,
.timer,
.player--mid-section,
.board,
.player--right-section,
.buttons,
.mana--container,
.enemy-section--hero {
  display: flex;
  justify-content: center;
  align-items: center;
}
body {
  font-family: "Press Start 2P", cursive;
  height: 100vh;
  overflow: hidden;
  background-image: url("/src/img/background-dark.png");
  background-size: cover;
}
/* game menu */
.game-menu {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.game-menu--container {
  z-index: 1002;
  cursor: pointer;
}
.game-menu--container--logo {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin-bottom: 50px;
  width: 800px;
  margin-top: -6%;
}
/* logo animation */
@keyframes floating {
  0% {
    transform: translate(0, -30px);
  }
  50% {
    transform: translate(0, 30px);
  }
  100% {
    transform: translate(0, -30px);
  }
}
a {
  text-decoration: none;
}
.game-menu--container--text {
  color: grey;
  text-align: center;
  padding: 40px;
  display: block;
  transition: all 1s;
  color: var(--menu-color);
  position: absolute;
  width: 100%;
  bottom: -100px;
  font-size: 30px;
}
.game-menu--container:hover .game-menu--container--text {
  font-size: 35px;
  color: #fad448;
}
.game-menu--background {
  position: absolute;
  background-image: url("../img/background-dark.png");
  background-size: cover;
  height: 100vh;
  width: 100vw;
  z-index: 1001;
}
.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1003;
  bottom: 0px;
  font-size: 30px;
  transition: all 3s;
}
.fa-brands {
  font-size: 40px;
}
.fa-brands:hover {
  opacity: 0.7;
  cursor: pointer;
}
.fa-brands:active {
  opacity: 0.5;
}
.footer--author {
  color: var(--menu-color);
  font-size: 12px;
  padding: 12px;
}
/* the actual game */
.game {
  opacity: 0;
  transition: all 5s;
}
.next-turn--container {
  position: absolute;
  width: 100vw;
  z-index: 1;
  max-width: 1920px;
  height: 100vh;
  max-height: 1080px;
  display: none;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 1s;
}
.next-turn--text {
  color: white;
  text-shadow: #388eff 10px 10px 0;
  font-size: 100px;
}
.enemy-side,
.player-side {
  display: flex;
}
.enemy-side {
  height: 40vh;
  max-height: 432px;
}
.enemy-side:hover .enemy-section--discription {
  opacity: 1;
}
.enemy-section {
  background-image: url("../img/enemy-background.png");
  border-radius: 50px 50px 0px 0;
  background-size: cover;
  border-top: ridge rgb(171, 0, 0);
  border-right: ridge rgb(171, 0, 0);
  border-left: ridge rgb(171, 0, 0);
  border-bottom: solid rgb(48, 24, 0);
  border-width: 20px 20px 5px 20px;
  position: relative;
}
.enemy-section--hero {
  height: 240px;
  margin: 50px;
  flex-direction: column;
}
.enemy-section--hero--img {
  height: inherit;
  border: ridge 20px rgb(171, 0, 0);
}
.enemy-section--hero--health {
  border: ridge 20px rgb(171, 0, 0);
  color: rgb(171, 0, 0);
  font-weight: bold;
  font-size: 40px;
  padding: 10px 10px 5px 10px;
  background-color: white;
  margin-top: -50px;
}
.enemy-section--discription {
  line-height: 120%;
  padding: 10px;
  position: absolute;
  font-size: 20px;
  left: 5%;
  background-color: brown;
  bottom: 100px;
  border: ridge 10px rgb(171, 0, 0);
  transition: all 0.3s;
  opacity: 0;
}

.turn-counter {
  font-size: 50px;
  text-align: center;
}

.player-side {
  height: 60vh;
  max-height: 648px;
}
.enemy-section,
.player--mid-section {
  width: 70vw;
  max-width: 1344px;
  background-color: aqua;
}
.timer {
  font-size: 80px;
}
.timer,
.turn-counter,
.player--left-section,
.player--right-section {
  text-shadow: 3px 3px var(--player-color);
  width: 15vw;
  max-width: 288px;
  margin-top: -50px;
  color: white;
}
.player--mid-section {
  position: relative;
  flex-direction: column;
  justify-content: flex-start;
  background-image: url("../img/player-background.png");
  border-top: solid rgb(48, 24, 0);
  border-right: ridge var(--player-color);
  border-left: ridge var(--player-color);
  border-bottom: ridge var(--player-color);
  border-width: 5px 20px 20px 20px;
  border-radius: 0 0 50px 50px;
  background-size: cover;
}

/* player left side styling */

.player--left-section {
  display: flex;
  align-items: center;
  row-gap: 25px;
  flex-direction: column;
  justify-content: center;
}
.deck-container,
.skull-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.skull {
  width: 140px;
}
.deck {
  width: 100px;
  border: ridge 8px var(--player-color);
  border-radius: 10px;
  margin-left: 20px;
}
.skull-number,
.deck-number {
  font-size: 40px;
  color: white;
  display: inline;
}

/* player board styling */

.board {
  margin-top: 2%;
  height: 110px;
}
.board--card {
  margin: 0 10px 0 10px;
  border: ridge 10px var(--player-color);
  height: 110px;
  position: relative;
}
.board--card--img {
  height: 100%;
}
.board--card--left-stat {
  color: white;
  left: -18px;
  background-color: var(--atk-color);
}
.board--card--turn-stat {
  color: white;
  right: -18px;
  background-color: var(--turn-color);
}

.board--card--turn-stat,
.board--card--left-stat {
  text-align: center;
  position: absolute;
  font-size: 30px;
  border: ridge 8px var(--player-color);
  border-radius: 50%;
  top: 60px;
}

/* player hero styling */

.hero {
  height: 175px;
  margin: 30px 0 10px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #ff3838;
}
.hero--img {
  border: ridge 15px var(--player-color);
  height: 100%;
}

.hero--health {
  border: ridge 15px var(--player-color);
  color: rgb(171, 0, 0);
  font-weight: bold;
  font-size: 40px;
  padding: 5px 5px 0 5px;
  background-color: white;
  margin-top: -40px;
  transition: background-color 1s;
}
/* hover-view styling */

.hover-view {
  border: ridge 15px var(--player-color);
  border-radius: 25px;
  position: absolute;
  height: 275px;
  width: 190px;
  top: -300px;
  right: -50px;
  padding: 8px;

  background-color: rgb(255, 230, 165);
  transition: all 0.5s;
  opacity: 0;
}
.hover-view:hover {
  display: none;
}
.hover-view--cost {
  position: absolute;
  color: rgb(255, 255, 255);
  border: ridge 10px var(--player-color);
  background-color: rgb(0, 26, 255);
  border-radius: 50%;
  top: -20px;
  font-size: 40px;
  left: -30px;
}
.hover-view--img {
  border: ridge 8px var(--player-color);
  height: 45%;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 5px;
  object-fit: cover;
}
.hover-view--name {
  font-size: 20px;
  text-align: center;
  font-weight: bold;
  line-height: 120%;
}
.hover-view--ability {
  padding: 0 10px;
  font-size: 15px;
  text-align: center;
}
.hover-view--left-stat {
  position: absolute;
  color: rgb(255, 255, 255);
  border: ridge 10px var(--player-color);
  background-color: var(--atk-color);
  border-radius: 50%;
  bottom: -20px;
  font-size: 40px;
  left: -30px;
  text-align: center;
}
.hover-view--turn-stat {
  position: absolute;
  color: rgb(255, 255, 255);
  border: ridge 10px var(--player-color);
  background-color: var(--turn-color);
  border-radius: 50%;
  bottom: -20px;
  font-size: 40px;
  right: -30px;
  text-align: center;
}

/* player hand styling*/

.hand {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 275px;
  width: 100%;
  position: absolute;
  bottom: -100px;
}
.hand--card {
  border: ridge 15px var(--player-color);
  border-radius: 25px;
  position: relative;
  height: 275px;
  width: 190px;
  top: 100px;
  padding: 8px;
  background-color: rgb(255, 230, 165);
  transition: all 0.5s;
  cursor: pointer;
  margin-right: 25px;
}
.hand--card:hover {
  top: -80px;
}
.hand--card--cost {
  position: absolute;
  color: rgb(255, 255, 255);
  border: ridge 10px var(--player-color);
  background-color: rgb(0, 26, 255);
  border-radius: 50%;
  top: -20px;
  font-size: 40px;
  left: -30px;
}

.hand--card--img {
  border: ridge 8px var(--player-color);
  border-radius: 10px;
  width: 100%;
  height: 45%;
  margin-bottom: 5px;
  object-fit: cover;
}
.hand--card--name {
  font-size: 18px;
  text-align: center;
  font-weight: bold;
  line-height: 120%;
}
.hand--card--ability {
  padding: 0 10px;
  font-size: 15px;
  text-align: center;
}
.hand--card--left-stat {
  left: -30px;
  background-color: var(--atk-color);
}
.hand--card--turn-stat {
  right: -30px;
  background-color: var(--turn-color);
}
.hand--card--turn-stat,
.hand--card--left-stat {
  position: absolute;
  color: rgb(255, 255, 255);
  border: ridge 10px var(--player-color);
  border-radius: 50%;
  bottom: -20px;
  font-size: 40px;
}

/* player right side*/
.player--right-section {
  flex-direction: column;
}
.buttons {
  flex-direction: column;
  padding: 30px 0;
}
.mana--counter {
  border: ridge 15px var(--player-color);
  border-radius: 25px;
  background-color: var(--cost-color);
  padding: 10px 20px;
  color: white;
  font-size: 30px;
  text-shadow: none;
}
.mana--container {
  gap: 20px;
  flex-wrap: wrap;
  row-gap: 10px;
  padding: 15px 0;
}

.mana {
  border-radius: 50%;
  border: solid 3px var(--player-color);
  padding: 15px;
  background: rgb(0, 167, 255);
  background: radial-gradient(
    circle,
    rgba(0, 167, 255, 1) 0%,
    rgba(86, 9, 121, 0.6727065826330532) 50%,
    rgba(0, 232, 255, 0) 100%
  );
}

.buttons button {
  font-size: 20px;
  padding: 15px 50px;
  font-family: "Press Start 2P", cursive;
  color: white;
  margin: 10px;
  background-color: var(--cost-color);
  border: ridge 15px var(--player-color);
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.2s;
}

.buttons button:hover {
  background-color: #ffffff;
  color: black;
}

.end-game--message,
.end-game--refresh {
  color: #9b842d;
  text-align: center;
}
.end-game--message {
  font-size: 150%;
  padding: 50px;
}
.healer {
  color: white;
  background-color: var(--heal-color);
}
.shake-animation {
  animation: tilt-n-move-shaking 0.15s infinite;
}
@keyframes tilt-n-move-shaking {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(5px, 5px) rotate(5deg);
  }
  50% {
    transform: translate(0, 0) rotate(0eg);
  }
  75% {
    transform: translate(-5px, 5px) rotate(-5deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

