    border-width: 8px 8px 2px 8px;
  }
  .player--mid-section {
    border-width: 2px 8px 8px 8px;
  }
  .buttons button {
    font-size: 13px;
    padding: 7px 20px;
    border-radius: 10px;
    margin: 2px;
    border-width: 8px;
  }
  .mana--counter {
    font-size: 15px;
    border-radius: 10px;
    border-width: 10px;
  }
  .mana--container {
    gap: 10px;
    row-gap: 5px;
  }
  .mana {
    padding: 10px;
  }
  .enemy-section--hero {
    height: 150px;
  }
  .enemy-section--hero--img {
    border-width: 10px;
  }
  .enemy-section--hero--health {
    font-size: 20px;
    border-width: 10px;
    padding: 5px 5px 1px 5px;
    margin-top: -30px;
  }
  .board {
    height: 65px;
  }
  .board--card {
    height: 65px;
    margin: 0 2px 0 2px;
    border-width: 5px;
  }
  .board--card--turn-stat,
  .board--card--left-stat {
    font-size: 15px;
    border-width: 5px;
    border-radius: 50%;
    top: 40px;
  }
  .board--card--left-stat {
    left: -8px;
  }
  .board--card--turn-stat {
    right: -8px;
  }
  .hero {
    height: 80px;
    margin: 15px 0 5px 0;
  }
  .hero--img {
    border-width: 8px;
  }
  .hero--health {
    font-size: 20px;
    border-width: 8px;
    margin-top: -25px;
  }
  .hover-view {
    height: 100px;
    width: 80px;
    top: -125px;
    padding: 2px;
    border-width: 5px;
    right: -10px;
    border-radius: 10px;
  }
  .hover-view--cost {
    border-width: 5px;
    font-size: 14px;
    left: -8px;
    top: -12px;
  }
  .hover-view--img {
    border-width: 2px;
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
  }
  .hover-view--name {
    font-size: 6px;
  }
  .hover-view--ability {
    font-size: 5px;
  }
  .hover-view--left-stat {
    border-width: 5px;
    font-size: 14px;
    left: -8px;
    bottom: -12px;
  }
  .hover-view--turn-stat {
    border-width: 5px;
    font-size: 14px;
    right: -8px;
    bottom: -12px;
  }
  .hand--card {
    height: 144px;
    width: 100px;
    border-width: 7px;
    border-radius: 15px;
    padding: 3px;
    top: 20px;
  }
  .hand--card:hover {
    top: -100px;
  }
  .hand--card--turn-stat,
  .hand--card--left-stat,
  .hand--card--cost {
    border-width: 7px;
    font-size: 20px;
  }
  .hand--card--cost,
  .hand--card--left-stat {
    left: -15px;
  }
  .hand--card--turn-stat {
    right: -15px;
  }
  .hand--card--img {
    border-width: 4px;
    border-radius: 10px;
  }
  .hand--card--name {
    font-size: 8px;
  }
  .hand--card--ability {
    font-size: 6px;
  }
}

/* DEVICES NOT SUPPPORTED */
@media (min-width: 0px) and (max-width: 767px) {
  .modal-viewport-error {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .game {
    display: none;
  }
  .game-menu {
    display: none;
  }
}
@media (height <= 400px) {
  .modal-viewport-error {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .game {
    display: none;
  }
  .game-menu {
    display: none;
  }
}
